<template>
  <div id="app">
    <v-app>
      <v-content class="container align-center px-1">
        <h2 class="font-weight-light mb-2"> COSMOS ODYSSEY </h2>
        <p>Solar System Travel Deals</p>
        <v-card>
          <v-card-title>
            Flights
            <v-spacer></v-spacer>
            <div>
              <v-select v-model="selectedHeaders" :items="headers" label="Select Columns" multiple outlined
                        return-object>
                <template v-slot:selection="{ item, index }">
                  <v-chip v-if="index < 5">
                    <span>{{ item.text }}</span>
                  </v-chip>
                  <span v-if="index === 5" class="grey--text caption">(+{{ selectedHeaders.length - 2 }} others)</span>
                </template>
              </v-select>
            </div>
          </v-card-title>

          <v-data-table :headers="showHeaders"
                        dense
                        hover
                        :search="search"
                        :items="items" mobile-breakpoint="800"
                        class="elevation-1"
                        inline
                        :loading="loadTable"
                        loading-text="Loading... Please wait"
                        :fields="visibleFields">

            <template v-slot:item.providerPrice="{ item }">
              <v-chip :color="getColor(item.providerPrice)"
                      dark> {{ item.providerPrice }}
              </v-chip>
            </template>

            <v-snackbar v-model="snackbar">{{ text }}
              <template v-slot:action="{ attrs }">
                <v-btn
                    color="pink"
                    text
                    v-bind="attrs"
                    @click="snackbar = false">
                  Close
                </v-btn>
              </template>
            </v-snackbar>

            <!--            crud operations-->
            <template v-slot:item.actions="{ item }">
              <div class="text-truncate">
                <v-btn
                    small
                    class="mr-2"
                    @click="showEditDialog(item)"
                    color="primary"
                >
                  Book this
                  <v-icon
                      dark
                      right
                  >
                    mdi-checkbox-marked-circle
                  </v-icon>
                </v-btn>

                <v-icon
                    small
                    @click="deleteItem(item)"
                    color="pink"
                >
                  mdi-delete
                </v-icon>
              </div>
            </template>

            <template v-slot:item.details="{ item }">
              <div class="text-truncate" style="width: 180px">
                {{ item.Details }}
              </div>
            </template>
            <template v-slot:item.url="{ item }">
              <div class="text-truncate" style="width: 180px">
                <a :href="item.URL" target="_new">{{ item.URL }}</a>
              </div>
            </template>

          </v-data-table>

          <v-dialog v-model="dialog" max-width="500px">
            <template v-slot:activator="{ on }">
              <div class="d-flex">
                <v-btn color="primary" dark class="ml-auto ma-3" v-on="on">
                  New
                  <v-icon small>mdi-plus-circle-outline</v-icon>
                </v-btn>
              </div>
            </template>
            <v-card>
              <v-card-title class="caption">
                <span v-if="editedItem.id">Booking to: {{ editedItem.id }}</span>
                <span v-if="editedItem.id">From: {{ editedItem.routeFromName }}</span>
                <span v-if="editedItem.id">To: {{ editedItem.routeToName }}</span>
                <span v-if="editedItem.id">Leaves: {{ editedItem.providerFlightStart }}</span>
                <span v-if="editedItem.id">Arrives: {{ editedItem.providerFlightEnd }}</span>
                <span v-if="editedItem.id">Quoted price: {{ editedItem.providerPrice }}</span>
                <span v-if="editedItem.id">Travel time: {{ editedItem.route }}</span>
                <span v-if="editedItem.id">Transport Company: {{ editedItem.rout }}</span>
                <span v-else>Create</span>
              </v-card-title>
              <v-card-text>
                <v-row>
                  <v-col cols="12" sm="4">
                    <v-text-field v-model="editedItem.Name" label="Name"></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="8">
                    <v-text-field v-model="editedItem.Details" label="Details"></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="12">
                    <v-text-field v-model="editedItem.URL" label="URL"></v-text-field>
                  </v-col>
                </v-row>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="showEditDialog()">Cancel</v-btn>
                <v-btn color="blue darken-1" text @click="saveItem(editedItem)">Save</v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>

        </v-card>
      </v-content>
    </v-app>
  </div>
</template>

<script>
import {get, post} from "axios";

const apiToken = "keyZIIVNiQPvozEWb"
const airTableApp = "appXJzFFs2zgj4X5C"
const airTableName = "Example"

export default {
  data() {
    return {
      headers: [
        {text: 'Action', value: 'actions', sortable: false},
        {text: 'ID', value: 'id'},
        {text: 'Expires', value: 'validUntil'},
        {text: 'Legs ID', value: 'legsId'},
        {text: 'Route ID', value: 'routeId'},
        {text: 'Route From ID', value: 'routeFromId'},
        {text: 'Route From Name', value: 'routeFromName'},
        {text: 'Route To Id', value: 'routeToId'},
        {text: 'Route To Name', value: 'routeToName'},
        {text: 'Route Distance', value: 'routeDistance'},
        {text: 'Provider ID', value: 'providerId'},
        {text: 'Provider Price', value: 'providerPrice'},
        {text: 'Provider Flight Start', value: 'providerFlightStart'},
        {text: 'Provider Flight End', value: 'providerFlightEnd'},
        {text: 'Provider Company ID', value: 'providerCompanyId'},
        {text: 'Provider Name', value: 'providerCompanyName'},
      ],

      headersMap: {
        action: {text: 'Action', value: 'actions', sortable: false},
        id: {text: 'ID', value: 'id', checked: false},
        validUntil: {text: 'Expires', value: 'validUntil'},
        legsId: {text: 'legsId', value: 'legsId'},
        routeId: {text: 'routeId', value: 'routeId'},
        routeFromId: {text: 'routeFromId', value: 'routeFromId'},
        routeFromName: {text: 'From', value: 'routeFromName'},
        routeToId: {text: 'routeToId', value: 'routeToId'},
        routeToName: {text: 'Where', value: 'routeToName'},
        routeDistance: {text: 'Distance (km)', value: 'routeDistance'},
        providerId: {text: 'providerId', value: 'providerId'},
        providerPrice: {text: 'Price ($)', value: 'providerPrice'},
        providerFlightStart: {text: 'Leaves (date)', value: 'providerFlightStart'},
        providerFlightEnd: {text: 'Arrives (date)', value: 'providerFlightEnd'},
        providerCompanyName: {text: 'Provider Name', value: 'providerCompanyName'},
        providerCompanyId: {text: 'providerId', value: 'providerCompanyId'},
      },
      items: [],
      selectedHeaders: [],
      dialog: false,
      editedItem: {},
      loadTable: true,
      search: '',
      snackbar: false,
      text: `Booked you on flight!`,

    }
  },
  computed: {
    //Done to get the ordered headers
    showHeaders() {
      return this.headers.filter(s => this.selectedHeaders.includes(s));
    }
  },
  created() {
    this.headers = Object.values(this.headersMap);
    this.selectedHeaders = this.headers;
  },

  mounted() {
    this.loadItems()
  },

  methods: {
    getColor(price) {
      if (price > 2500000) return 'red'
      else if (price > 100000) return 'orange'
      else return 'green'
    },


    showEditDialog(item) {
      this.editedItem = item || {}
      this.dialog = !this.dialog
    },

    loadItems() {
      get('api/serverinfo')
          .then((response) => {
            this.loadTable = false;
            this.items = response.data.map((item) => {
              return {
                id: item.id,
                validUntil: item.validUntil,
                legsId: item.legsId,
                routeId: item.routeId,
                routeFromId: item.routeFromId,
                routeFromName: item.routeFromName,
                routeToId: item.routeToId,
                routeToName: item.routeToName,
                routeDistance: item.routeDistance,
                providerId: item.providerId,
                providerPrice: item.providerPrice,
                providerFlightStart: item.providerFlightStart,
                providerFlightEnd: item.providerFlightEnd,
                providerCompanyId: item.providerCompanyId,
                providerCompanyName: item.providerCompanyName
              }
            })
          }).catch((error) => {
        console.log(error)
      })
    },
    saveItem(item) {
      /* this is used for both creating and updating API records
       the default method is POST for creating a new item */

      let method = "post"
      let url = `https://api.airtable.com/v0/${airTableApp}/${airTableName}`
      let id = item.id

      // airtable API needs the data to be placed in fields object
      let data = {
        fields: item
      }

      if (id) {
        // if the item has an id, we're updating an existing item
        method = "patch"
        url = `https://api.airtable.com/v0/${airTableApp}/${airTableName}/${id}`

        // must remove id from the data for airtable patch to work
        delete data.fields.id
      }

      // save the record
      axios[method](url,
          data,
          {
            headers: {
              Authorization: "Bearer " + apiToken,
              "Content-Type": "application/json"
            }
          }).then((response) => {
        if (response.data && response.data.id) {
          console.log(response.data)
          // add new item to state
          this.editedItem.id = response.data.id
          if (!id) {
            // add the new item to items state
            this.items.push(this.editedItem)
          }
          this.editedItem = {}
        }
        this.dialog = !this.dialog
      })
    },

    deleteItem(item) {
      //console.log('deleteItem', item)
      let id = item.id
      let idx = this.items.findIndex(item => item.id === id)
      if (confirm('Are you sure you want to delete this?')) {
        /* not really deleting in API for demo */
        /*
        axios.delete(`https://api.airtable.com/v0/${airTableApp}/${airTableName}/${id}`,
            { headers: {
                Authorization: "Bearer " + apiToken,
                "Content-Type": "application/json"
            }
        }).then((response) => {
            this.items.splice(idx, 1)
        })*/
        this.items.splice(idx, 1)
      }
    },
  }
}
</script>